.App {
  text-align: center;
  font-family: 'Oswald', sans-serif;
}

.App-logo {
  height: 40vmin;
}



.App-link {
  color: #09d3ac;
}

.node {
  width: 1.6em;
  height: 1.6em;
  margin: 3px;
  display: inline-block;
  border: 2px solid #f75e73;
  border-radius: 4px;
  transition: background-color 0.3s ease-out;
  cursor: pointer;
}

.node-finish {
  background-image: url("/finish.svg");
  background-repeat: no-repeat;
  background-size: 1.5em;
  border: 0px;
  animation-name: startFinishAnimation;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

.node-start {
  background-image: url("/start.svg");
  background-repeat: no-repeat;
  background-size: 1.5em;
  border: 0px;
  animation-name: startFinishAnimation;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

@keyframes startFinishAnimation {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1.1);
  }
}

.node-wall {
  animation-name: wallAnimation;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
  background-color: #424557;
  border: 2px solid #546E7A;
  border-radius: 4px;
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.5);
    background-color: #3d1c3c9f;
    border-radius: 80%;
  }
  50% {
    border-radius: 60%;
    background-color: #9c264b9f;
  }
  75% {
    transform: scale(1.1);
    border-radius: 30%;
    background-color: #d8445873;
  }
  100% {
    transform: scale(1);
    background-color: #ec667871;
  }
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
  background-color: #e9a05bb7;
  border: 2px solid #f07731e3;
  border-radius: 4px;
}

@keyframes shortestPath {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
